import React from "react";
import styled, { keyframes } from "styled-components";
import product1 from "../../../image/image-11.png";
import product2 from "../../../image/image-13.png";
import product3 from "../../../image/image-14.png";
import product4 from "../../../image/image-12.png";
import Button from "../../common/Button/Button";
import Theme from "../../common/Theme/Theme";

const isLoading = (loading) => (loading === "true" ? true : false);

const MarketContainer = styled.div`
  background: ${Theme.color.lightGrey};
  padding: 4rem 7.5rem;
`;
const ProductWrapper = styled.div`
  display: flex;
  justify-content: space-around;
`;
const ProductInfo = styled.div`
  max-width: 300px;
`;
const ProductCategory = styled.div`
  color: ${Theme.color.grey};
  margin-bottom: 1.5rem;
`;
const ProductName = styled.div`
  font-size: 3rem;
  font-weight: bolder;
  margin-bottom: 1rem;
`;
const ProductPrice = styled.div`
  font-size: 1.5rem;
  font-weight: bolder;
  color: ${Theme.color.grey};
  margin-bottom: 2rem;
`;

const cart = keyframes`
  12.5% {
      transform: translateX(-60px) rotate(-18deg);
  }
  25%,
  45%,
  55%,
  75% {
      transform: none;
  }
  50% {
      transform: scale(.9);
  }
  44%,
  56% {
      transform-origin: 12px 23px;
  }
  45%,
  55% {
      transform-origin: 50% 50%;
  }
  87.5% {
      transform: translateX(70px) rotate(-18deg);
  }
  100% {
      transform: translateX(140px) rotate(-18deg);
  }
`;

const ProductButton = styled.button`
  position: relative;
  border: none;
  background: none;
  padding: 8px 28px;
  border-radius: 8px;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  overflow: hidden;
  cursor: pointer;
  text-align: center;
  min-width: 144px;
  color: ${Theme.color.white};
  background: ${Theme.color.blue};
  transform: scale(1);
  transition: transform 0.4s cubic-bezier(0.36, 1.01, 0.32, 1.27);

  &:active {
    transform: scale(0.95);
  }

  box-sizing: border-box;
  & > * {
    box-sizing: border-box;
    &:before,
    &:after {
      box-sizing: inherit;
    }
  }
`;

const ProductButtonText = styled.span.attrs((props) => ({
  w: isLoading(props.loading) ? "2px" : "14px",
  h: isLoading(props.loading) ? "14px" : "2px",
  l: isLoading(props.loading) ? "8px" : "2px",
  t: isLoading(props.loading) ? "6px" : "12px",
  icon_b: isLoading(props.loading) ? "180deg" : "0deg",
  icon_a: isLoading(props.loading) ? "270deg" : "90deg",
  span_y: isLoading(props.loading) ? "-32px" : "0",
}))`
  font-size: 14px;
  font-weight: 500;
  display: block;
  position: relative;
  padding-left: 24px;
  margin-left: -8px;
  line-height: 26px;
  transform: ${(props) => `translateY(${props.span_y})`};
  transition: transform 0.7s ease;
  &:before {
    content: "";
    width: ${(props) => props.w};
    height: ${(props) => props.h};
    left: ${(props) => props.l};
    top: ${(props) => props.t};
    border-radius: 1px;
    position: absolute;
    background: currentColor;
    transform: scale(0.75) ${(props) => `rotate(${props.icon_b})`};
    transition: transform 0.65s ease 0.05s;
  }
  &:after {
    content: "";
    width: ${(props) => props.w};
    height: ${(props) => props.h};
    left: ${(props) => props.l};
    top: ${(props) => props.t};
    border-radius: 1px;
    position: absolute;
    background: currentColor;
    transform: scale(0.75) ${(props) => `rotate(${props.icon_a})`};
    transition: transform 0.65s ease 0.05s;
  }
`;

const ProductCart = styled.div.attrs((props) => ({
  tick: Theme.color.blue,
  cartColor: Theme.color.white,
  fill: isLoading(props.loading) ? 1 : 0,
  fill_d: isLoading(props.loading) ? "0.8s" : "",
  offset: isLoading(props.loading) ? "0" : "10px",
  offset_d: isLoading(props.loading) ? "0s" : "1.73s",
}))`
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -13px 0 0 -18px;
  transform-origin: 12px 23px;
  transform: translateX(-120px) rotate(-18deg);

  &:before,
  &:after {
    content: "";
    position: absolute;
  }
  &:before {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    box-shadow: inset 0 0 0 2px ${(props) => props.cartColor};
    bottom: 0;
    left: 9px;
    filter: ${(props) => `drop-shadow(11px 0 0 ${props.cartColor})`};
  }
  &:after {
    width: 16px;
    height: 9px;
    background: ${(props) => props.cartColor};
    left: 9px;
    bottom: 7px;
    transform-origin: 50% 100%;
    transform: perspective(4px) rotateX(-6deg)
      ${(props) => `scaleY(${props.fill})`};
    transition: transform 1.2s ease ${(props) => props.fill_d};
  }
  svg {
    z-index: 1;
    width: 36px;
    height: 26px;
    display: block;
    position: relative;
    fill: none;
    stroke: ${(props) => props.cartColor};
    stroke-width: 2px;
    stroke-linecap: round;
    stroke-linejoin: round;
    polyline {
      &:last-child {
        stroke: ${(props) => props.tick};
        stroke-dasharray: 10px;
        stroke-dashoffset: ${(props) => props.offset};
        transition: stroke-dashoffset 0.4s ease 1.73s;
      }
    }
  }

  &.loading {
    animation: ${cart} 3.4s linear forwards 0.2s;
  }
`;

const ProductImageWrapper = styled.div`
  display: inherit;
  width: 30rem;
  height: 30rem;
  justify-content: center;
  background: ${Theme.color.blue};
  border-radius: 50%;
  margin-right: 5%;
`;
const ProductImage = styled.img`
  width: 30rem;
  height: 30rem;
`;
const ProductList = styled.div`
  display: inherit;
  flex-direction: column;
  justify-content: center;
`;
const ProductItemImage = styled.img`
  width: 5rem;
  & + & {
    margin-top: 1rem;
  }
`;
const ProductRecommendList = styled.div`
  padding: 2rem 4rem;
`;
const ProductRecommendItem = styled.div`
  display: flex;
`;
const ProductRecommendItemImageWrapper = styled.div`
  width: 4rem;
  height: 4rem;
  background: ${Theme.color.yellow};
  border-radius: 5px;
  margin-right: 20px;
`;
const ProductRecommendItemImage = styled(ProductItemImage)`
  width: 5rem;
  height: 5rem;
  transform: translate(-10%, -10%);
  object-fit: fill;
`;
const ProductRecommendItemInfo = styled.div`
  display: flex;
  flex-wrap: wrap;
`;
const ProductRecommendItemName = styled(ProductName)`
  font-size: 0.9rem;
  width: 100%;
`;
const ProductRecommendItemPrice = styled(ProductPrice)`
  font-size: 0.8rem;
`;
const PlusButton = styled(Button)`
  background: ${Theme.color.lightGrey};
  width: 1rem;
  height: 1rem;
  font-size: 1.3rem;
  color: ${Theme.color.grey};
`;

const Market = () => {
  const DummyData = [
    {
      category: "Men's Running Shoe",
      id: 1,
      name: "Nike Joyride Run Flyknit",
      price: `$180`,
      image: product1,
    },
    {
      category: "Men's Running Shoe",
      id: 2,
      name: "Nike Joyride Run Flyknit2",
      price: `$200`,
      image: product3,
    },
    {
      category: "Men's Running Shoe",
      id: 3,
      name: "Nike Joyride Run Flyknit3",
      price: `$250`,
      image: product2,
    },
  ];

  const currentDisplay = DummyData[0];
  const [loading, setLoading] = React.useState(false);

  return (
    <MarketContainer>
      {/* 상품 정보 및 리스트 */}
      <ProductWrapper>
        {/* 상품 정보 */}
        <ProductInfo>
          <ProductCategory>{currentDisplay.category}</ProductCategory>
          <ProductName>{currentDisplay.name}</ProductName>
          <ProductPrice>{currentDisplay.price}</ProductPrice>
          <ProductButton
            onClick={() => {
              setLoading((load) => !load);
              setTimeout(() => {
                setLoading((load) => !load);
              }, 3700);
            }}
            loading={loading.toString()}
          >
            <ProductButtonText loading={loading.toString()}>
              Add To Cart
            </ProductButtonText>
            <ProductCart
              className={loading ? "loading" : ""}
              loading={loading.toString()}
            >
              <svg viewBox="0 0 36 26">
                <polyline points="1 2.5 6 2.5 10 18.5 25.5 18.5 28.5 7.5 7.5 7.5"></polyline>
                <polyline points="15 13.5 17 15.5 22 10.5"></polyline>
              </svg>
            </ProductCart>
          </ProductButton>
        </ProductInfo>

        {/* 상품 이미지 */}
        <ProductImageWrapper>
          <ProductImage src={currentDisplay.image} alt={currentDisplay.name} />
        </ProductImageWrapper>

        {/* 상품 리스트 */}
        <ProductList>
          {DummyData.map((elem, i) => (
            <ProductItemImage key={i} src={elem.image} alt={elem.name} />
          ))}
        </ProductList>
      </ProductWrapper>

      {/* 상품 추천 리스트 */}
      <ProductRecommendList>
        <ProductRecommendItem>
          {/* IMAGE */}
          <ProductRecommendItemImageWrapper>
            <ProductRecommendItemImage src={product4} alt="" />
          </ProductRecommendItemImageWrapper>
          {/* INFO */}
          <ProductRecommendItemInfo>
            <ProductRecommendItemName>
              Air Jordan 6 Retro
            </ProductRecommendItemName>
            <ProductRecommendItemPrice>$225</ProductRecommendItemPrice>
            <PlusButton>+</PlusButton>
          </ProductRecommendItemInfo>
        </ProductRecommendItem>
      </ProductRecommendList>
    </MarketContainer>
  );
};

export default Market;
