import React from "react";
import styled from "styled-components";
import Button from "../common/Button/Button";
import Theme from "../common/Theme/Theme";

const HeaderContainer = styled.header`
  width: 100%;
  height: 70px;
  background: ${Theme.color.lightGrey};
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
`;
const Wrapper = styled.div`
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;
const LeftSide = styled.div`
  margin-left: 40px;
`;
const RightSide = styled.div`
  margin-right: 40px;
`;
const Brand = styled(Button)`
  background: ${Theme.color.lightgrey};
  font-size: 1.2rem;
`;
const Feature = styled(Button)`
  background: ${Theme.color.lightgrey};
`;
const SignUpButton = styled(Button)`
  padding: 0.8rem 1.2rem;
  background: ${Theme.color.yellow};
  &:hover {
    background: ${Theme.color.lightYellow};
  }
`;
const SignUp = styled.a`
  text-decoration: none;
  color: ${Theme.color.white};
`;
const CurrentPercent = styled.div`
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4px;
  background: ${Theme.color.yellow};
  transition: transform 0.3s ease-out;
  transform: ${(props) => `translateX(-${props.scrollBarPercent}%)`};
`;

const Header = ({ scrollBarPercent }) => {
  return (
    <HeaderContainer>
      <Wrapper>
        <LeftSide>
          <Brand>Kraft8</Brand>
          <Feature>Explore</Feature>
          <Feature>Work</Feature>
          <Feature>Services</Feature>
          <Feature>Contact</Feature>
        </LeftSide>
        <RightSide>
          <Feature>Login</Feature>
          <SignUpButton>
            <SignUp href="#">Sign Up</SignUp>
          </SignUpButton>
        </RightSide>
        <CurrentPercent scrollBarPercent={scrollBarPercent} />
      </Wrapper>
    </HeaderContainer>
  );
};

export default Header;
